.sell {
    align-items: center;
    display: flex;
    flex-direction: column;
    margin-top: 100px;
    section {
        position: relative;
        width: 60vw;
        height: 60vh;
        box-shadow: 0px 0px 30px #c0abe6;
        border-radius: 15px;
        overflow: hidden;
        background: rgb(59, 69, 116);
        background: linear-gradient(
          135deg,
          rgba(59, 69, 116, 1) 0%,
          rgba(113, 114, 166, 1) 100%
        );
        img:not(.pila-libros) {
            padding: 25px;
            width: 35%;
        }
        
    }
    article {
        display: flex;
        flex-direction: column;
        margin-bottom: 15px;
    }
    label:not(:last-child) {
        font-weight: bold;
        margin-bottom: 5px;
    }
    .sell__form-inputs input[type="text"],
    input[type="number"] {
        border: solid 1px #c5c5c5;
        border-radius: 5px;
        height: 40px;
        margin-right: 5px;
        outline: none;
        width: 220px;
    }
    .sell__select {
      width: 100%;
    }
}
  
.sell__container-left {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    width: 50%;
    .sell__icon {
        color: white;
        font-size: 40px;
        margin-bottom: 15px;
        text-shadow: 1px 1px 2px black;
    }
    p {
        text-align: center;
        width: 80%;
        color: white;
        font-size: 35px;
        font-weight: 900;
        text-shadow: 1px 1px 2px black;
    }
}

  
.sell__pila-libros {
    padding: 5px;
    height: 100px;
}
  
  
  
.sell__hero {
    display: flex;
    justify-content: flex-end;
    position: absolute;
    height: 100%;
    bottom: 0;
    right: 0;
    z-index: 0;
}

.sell__trian {
    width: 0;
    height: 0;
    border-left: 7vh solid transparent;
    bottom: 0px;
    border-bottom: 80vh solid white;
  }
  
.sell__container-form {
    background-color: white;
    color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 30vw;
    h2 {
        font-size: 35px;
        font-weight: bolder;
        margin-bottom: 10px;
        color: rgb(59, 69, 116);
        text-shadow: 0.2px 0.2px 0.5px black;
    }
    button {
        background-color: rgb(59, 69, 116);
        border-radius: 5px;
        color: whitesmoke;
        font-size: 25px;
        font-weight: bold;
        margin-top: 30px;
        padding: 15px 35px;
        transition-property: font-size, box-shadow;
        transition-duration: 0.5s;
        &:hover {
            cursor: pointer;
            box-shadow: 10px 10px 8px 0px rgba(0, 0, 0, 0.1);
            font-size: 28px;
            color: whitesmoke;
        }
    }
}
  
.sell__form-inputs div {
    display: flex;
}


  
.sell__cont-estados {
    display: flex;
    justify-content: space-around;
    width: 220px;
    padding: 5px;
    div input {
        height: 25px;
        width: 25px;
    }
    div label {
        font-size: 16px;
        padding: 5px;
    }
}
  
.sell__submit {
    display: flex;
    justify-content: center;
}
  
  
/* The container */
.sell__terminos {
    align-items: center;
    position: relative;
    padding-left: 35px;
    cursor: pointer;
    display: flex;
    font-size: 20px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
    }
}

  /* Create a custom checkbox */
  .sell__checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
  }
  
  /* On mouse-over, add a grey background color */
  .sell__terminos:hover input ~ .sell__checkmark {
    background-color: #ccc;
  }
  
  /* When the checkbox is checked, add a blue background */
  .sell__terminos input:checked ~ .sell__checkmark {
    background-color: rgb(59, 69, 116);
  }
  
  /* Create the checkmark/indicator (hidden when not checked) */
  .sell__checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  /* Show the checkmark when checked */
  .sell__terminos input:checked ~ .sell__checkmark:after {
    display: block;
  }
  
  /* Style the checkmark/indicator */
  .sell__terminos .sell__checkmark:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }
  
  @media (max-width: 414px) {
    .sell section {
      background: rgb(59, 69, 116);
      background: linear-gradient(
        90deg,
        rgba(59, 69, 116, 1) 0%,
        rgba(113, 114, 166, 1) 100%
      );
      height: 950px;
      width: 100%;
    }
    
    .sell {
      .sign-in,
      .trian,
      img {
        display: none;
      }
    }

    .sell__container-form h2 {
      background: rgb(59, 69, 116);
      background: linear-gradient(
        90deg,
        rgba(59, 69, 116, 1) 0%,
        rgba(113, 114, 166, 1) 100%
      );
      color: white;
      margin: 0 0 20px 0;
      padding: 10px 10px;
      width: 100%;
    }
  
    .sell form {
      padding: 0 10px;
    }
  
    .sell__form-inputs {
      margin-left: 15px;
      div {
      display: flex;
      flex-direction: column;
    }
    }

    .sell__trian {
      display: none;
    }
    
    .sell__select {
      height: 50px;
      width: 50% !important;
    }

    .sell__hero,
    .sell__container-form,
    .sell__container-form form {
      width: inherit;
      height: inherit;
    }
  
    .sell__container-form {
      justify-content: space-between;
    }
  
    .sell__form-inputs input {
      width: 100%;
    }
  
    .sell__container-form button {
      font-size: 30px;
    }
    .css-qc6sy-singleValue {
      position: relative;
      left: 30px;
      margin-top: 10px;
    }
    .css-1hb7zxy-IndicatorsContainer {
      position: relative;
      right: 50px;
    }
    .css-1okebmr-indicatorSeparator {
      height: 1px;
      margin: 0;
    }

    .sell__opt-estado div {
      flex-direction: row;
    }

    .sell__file {
      width: fit-content;
    }
  }