nav {
    background: url(../../assets/img-biblio.png);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
    display: flex;
    flex-direction: column;
    font-size: 20px;
    height: 200px;
    width: 100vw;
    box-sizing: border-box;
    img {
        width: 100%;
    }
    form {
        align-items: center;
        background-color: white;
        border: 1px solid grey;
        border-radius: 30px;
        display: flex;
        height: 30px;
        margin: 15px 0;
        padding: 10px;
        width: 45vw;
        input {
            border: none;
            height: 100%;
            outline: none;
            width: 100%;
        }
        button {
            background-color: rgba(0,0,0,0);
            border: none;
            height: 100%;
            transition: color 300ms ease-out;
        }
    }
}

.nav__container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 50%;
}

.nav__categories {
    display: flex;
    justify-content: center;
    ul {
        width: 100%;
        display: flex;
        justify-content: space-around;
        flex-flow: wrap;
        li {
            display: none;
            list-style: none;
            margin-bottom: 10px;
            text-align: center;
            opacity: 0;
            transition: opacity 300ms linear;
        }
        .nav__break {
            width: 100%;
        }
        .nav__show-categories {
            display: block;
            opacity: 1;
            transition: opacity 300ms linear;
        }
    }
}

.nav__logo {
    width: 100px;

}

.nav__links {
    list-style: none;
    display: flex;
    justify-content: space-between;
    padding: 0;
    width: 23vw;
}

.nav__mobile {
    display: none;
}

nav a, #categories{
    color: white;
    text-align: center;
    text-decoration: none;
    text-shadow: 2px 0px 10px black;
    transition: color 300ms ease-out;
}

.nav__links li span img {
    border-radius: 50%;
}

#categories:hover, li *:hover, form button:hover {
    color: rgb(116, 116, 116);
    cursor: pointer;
}

.nav__links * {
    transition: color 300ms ease-out;

}

@media screen and (max-width: 1300px) {
    nav {
        height: 100px;
    }
    .nav__container {
        height: 100%
    }
    .nav__mobile {
        display: block;
    }
    .nav__desktop {
        display: none;
    }
}