.error {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-top: 100px;
    
}
  
  .container-alert {
    width: 50%;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px lightgray solid;
    border-radius: 10px;
    box-shadow: 10px 10px 8px 0px rgba(0, 0, 0, 0.09);
  }
  
  .txt-oh-no {
    font-weight: 900;
    color: rgb(59, 69, 116);
    font-size: 50px;
  }
  
  .container-alert img {
    width: 50%;
  }
  
  .img-libro {
    box-shadow: 0 0 10px 10px white inset;
  }
  
.btn-regreso-inicio {
  background-color: rgb(59, 69, 116);
  text-decoration: none;
  font-weight: 900;
  color: whitesmoke;
  padding: 10px;
  border-radius: 5px;
  font-size: 20px;
  transition-property: font-size, background-color, box-shadow;
  transition-duration: 0.5s;
  margin: 5px;
  &:hover {
    font-size: 21px;
    box-shadow: 10px 10px 8px 0px rgba(0, 0, 0, 0.1);
  }  
}