.login__screen {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    * {
        box-sizing: border-box;
        margin: 0px;
        padding: 0px;
    }
    section {
        position: relative;
        width: 60vw;
        height: 60vh;
        box-shadow: 0px 0px 30px #c0abe6;
        border-radius: 15px;
        overflow: hidden;
        background: rgb(59, 69, 116);
        background: linear-gradient(
            135deg,
            rgba(59, 69, 116, 1) 0%,
            rgba(113, 114, 166, 1) 100%
        );
        margin-top: 100px;
        img {
            padding: 25px;
            position: absolute;
            width: 15%;
        }
    }
    article {
        display: flex;
        flex-direction: column;
        margin-bottom: 15px;
    }
    form {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        .login__form-inputs {
            width: 60%;
            input {
                border: solid 1px #c5c5c5;
                border-radius: 5px;
                height: 40px;
                margin-right: 5px;
                outline: none;
                width: 100%;
            }
            label {
                font-weight: bold;
                margin-bottom: 5px;
            }
        }
        button {
            background-color: rgb(59, 69, 116);
            border-radius: 5px;
            color: whitesmoke;
            font-size: 25px;
            font-weight: bold;
            display: flex;
            justify-content: center;
            margin-top: 30px;
            padding: 10px 35px;
            transition: all 300ms linear;
            &:hover {
                cursor: pointer;
                transform: scale(1.02);
                box-shadow: 0 0 15px black;
            }
        }
    }
}


.login__signup {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    width: 50%;
    * {
        color: white;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    h3 {
        text-shadow: 1px 1px 2px black;
        font-size: 35px;
    }
}

.login__registerbtn {
    text-decoration: none;
    background-color: rgb(59, 69, 116);
    border-radius: 5px;
    color: whitesmoke;
    font-size: 25px;
    font-weight: bold;
    padding: 10px 35px;
    transition: all 300ms linear;
    &:hover {
        cursor: pointer;
        transform: scale(1.02);
        box-shadow: 0 0 15px black;
    }
}

.login__hero {
    display: flex;
    justify-content: flex-end;
    position: absolute;
    height: 100%;
    bottom: 0;
    right: 0;
    z-index: 0;
}
.login__trian {
    width: 0;
    height: 0;
    border-left: 7vh solid transparent;
    bottom: 0px;
    border-bottom: 80vh solid white;
}

.login__signin {
    background-color: white;
    color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 30vw;
    .login__icon {
        color: rgb(59, 69, 116);
        font-size: 40px;
        text-shadow: 0.2px 0.2px 0.5px black;
        margin-bottom: 10px;
    }
}

/* Custom checkbox */

/* The container */
.login__terminos {
    align-items: center;
    position: relative;
    padding-left: 35px;
    cursor: pointer;
    display: flex;
    font-size: 20px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
        &:checked ~ span {
            background-color: rgb(59, 69, 116);
        }
        &:checked ~ .checkmark:after {
            display: block;
        }
    }
    span {
        position: absolute;
        top: 0;
        left: 0;
        height: 25px;
        width: 25px;
        background-color: #eee;
        &:after {
            content: "";
            position: absolute;
            display: none;
            left: 9px;
            top: 5px;
            width: 5px;
            height: 10px;
            border: solid white;
            border-width: 0 3px 3px 0;
            -webkit-transform: rotate(45deg);
            -ms-transform: rotate(45deg);
            transform: rotate(45deg);
        }
    }
    &:hover input ~ span {
        background-color: #ccc;
    }
}
.register__form-inputs div {
    display: flex;
    article:first-child {
        width: 50%;
        margin-right: 5px;
    }
}

.btn {
    border: 2px solid gray;
    color: gray;
    background-color: white;
    padding: 8px 20px 8px 2px;
    border-radius: 8px;
    font-size: 15px;
    font-weight: bold;
    text-align: left;
    transition: all 300ms linear;
    &:hover {
        color: black;
        cursor: pointer;
        border-color: black;
    }
}


// TODO media queries
@media (max-width: 414px) {
    .login__screen section {
        background: rgb(59, 69, 116);
        background: linear-gradient(
        90deg,
        rgba(59, 69, 116, 1) 0%,
        rgba(113, 114, 166, 1) 100%
        );
        height: 350px;
        width: 100%;
    }

    .login__register section {
        height: 900px;
    }

    .login__signin h2 {
        background: rgb(59, 69, 116);
        background: linear-gradient(
        90deg,
        rgba(59, 69, 116, 1) 0%,
        rgba(113, 114, 166, 1) 100%
        );
        color: white;
        margin: 0 0 20px 0;
        padding: 10px 10px;
        width: 100%;
    }

    .login__screen form {
        padding: 0 10px;
        input {
            width: 100%;
        }
    }

    .login__form-inputs div {
        display: flex;
        flex-direction: column;
    }

    .login__trian{
        display: none;
    }

    .login__hero,
    .login__signin,
    .login__signin form {
        width: inherit;
        height: inherit;
    }
}
