.cart__title {
    font-size: 35px;
    margin: 0;
    padding: 15px;
    span {
        margin-right: 5px;
    }
    
}

.cart__container {
    display: flex;
    justify-content: center;
}

.cart {
    background: rgb(59,69,116);
    background: linear-gradient(90deg, rgba(59,69,116,1) 0%, rgba(113,114,166,1) 100%);
    border-radius: 15px;
    box-shadow: 0px 0px 30px #C0ABE6;
    color: white;
    margin: 100px 0;
    width: 70%;
}

.cart__productos-container {
    background-color: #ffffff;
    color: #7C7B7C;
}

.cart__producto {
    align-items: center;
    display: flex;
    height: 150px;
    justify-content: space-between;
    transition: all 400ms ease-out;
    &:not(:last-child) {
        border-bottom: solid 1px #676294;
    }
    &:hover {
        background-color: #c5c5c5;
        color: white;
        cursor: pointer;
    }
}

.cart__producto-info {
    align-items: center;
    display: flex;
    height: 100%;
    text-align: left;
    width: 40%;
    img {
        margin: 0 10px;
        max-height: 90%;
    }
}

.cart__comprar {
    align-items: center;
    display: flex;
    height: 150px;
    justify-content: space-between;
    margin: 10px;
}

.cart__producto-title {
    font-size: 20px;
    font-weight: bold;
}

.cart__precio {
    font-size: 25px;
    font-weight: bold;
}

.cart__close {
    margin-right: 10px;
    transition: all 300ms linear;
    &:hover {
        color:#7C7B7C
    }
}

.cart__total-precio {
    background-color: white;
    display: flex;
    font-size: 30px;
    font-weight: bold;
    justify-content: flex-end;
    padding: 10px 10px 20px 0;
}
.cart__total{
    border: solid 2px #FF7AA5;
    color: #7C7B7C;
    padding: 10px 35px;
    text-transform: uppercase;
}

.cart__precio-final {
    background-color: #FF7AA5;
    padding: 10px 30px;
}

.cart__pagar-btn, .cart__seguir-btn {
    background-color: rgb(59, 69, 116);
    border-radius: 5px;
    color: whitesmoke;
    font-size: 35px;
    font-weight: bold;
    display: flex;
    justify-content: center;
    margin-top: 30px;
    padding: 10px 35px;
    transition: all 300ms linear;
    &:hover {
        cursor: pointer;
        transform: scale(1.02);
        box-shadow: 0 0 15px black;
    }
}

@media (max-width: 414px){

    .cart {
        border: none;
        margin-top:30px;
        width: 100vw;
    }

    .cart__producto-info {
        width: 50%
    }
    .cart__cantidad p:first-child{
        width: 0;
        overflow: hidden;
    }

    .cart__comprar{
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    }

    .cart__total {
        margin-bottom: 5px;
    }

    .cart__comprar-btn{
        width: 100%;
        margin: 0;
    }
    .cart__producto-info{
        width: auto;
    }
    
    .cart__precio-final, .cart__total {
        height: 90%;
        border: solid 2px #FF7AA5;
    }

    .cart__comprar{
        flex-direction: column-reverse;
        align-items: flex-end;
        justify-content: space-evenly
    }
}
