.category__category {
    display: flex;
    width: 100vw;
    justify-content: center;
    margin-top: 100px;
    section {
        background: rgb(59, 69, 116);
        background: linear-gradient(
            90deg,
            rgba(59, 69, 116, 1) 0%,
            rgba(113, 114, 166, 1) 100%
        );
        border-radius: 15px;
        box-shadow: 0px 0px 30px #c0abe6;
        padding-bottom: 70px;
        width: 60%;
        h2 {
            color: whitesmoke;
            margin-left: 10px;
        }
    }
}

.category__books {
    background-color: white;
    display: grid;
    grid-template-columns: repeat(5, 20%);
    padding: 30px 15px;
    width: 100%;
}

.category__book {
    cursor: pointer;
    min-width: 200px;
    width: 200px;
    position: relative;
    a {
      text-decoration: none;
      color: black;
    }
    img {
      width: 100%;
      height: 280px;
    }
    .home__heart {
      color: #676294;
      opacity: 0;
      position: absolute;
      right: 18px;
      top: 8px;
      transition: all 300ms linear;
      &:hover {
        color: #ff7aa5;
      }
    }
    p {
      color: #7c7b7c;
      font-size: 12px;
      transition: color 300ms ease-out;
    }
    &:hover p {
      color: black;
    }
    &:hover .home__heart {
      opacity: 1;
    }
  }

@media (max-width: 414px) {
  .category__category section {
    border: none;
    font-size: 20px;
    width: 100vw;
  }

  .category__books {
    grid-template-columns: repeat(2, 50%);
  }

  .category__book {
    min-width: 43vw;
    width: 43vw;
  }

}
