.book {
    display: flex;
    justify-content: center;
    margin-top: 100px;
    section {
        position: relative;
        width: 60vw;
        height: 60vh;
        box-shadow: 0px 0px 30px #c0abe6;
        border-radius: 15px;
        overflow: hidden;
        background: rgb(59, 69, 116);
        background: linear-gradient(
            135deg,
            rgba(59, 69, 116, 1) 0%,
            rgba(113, 114, 166, 1) 100%
        );
        *:not(.book__hero) {
            position: relative;
            z-index: 1;
        }   
    }
}
  


.book__details {
    color: white;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-around;
    padding: 10px;
    width: 55%;
}

.book__mobile-img {
    display: none;
}

.book__price {
    align-items: center;
    display: flex;
    justify-content: space-between;
    width: 220px;
    height: 60px;
    h4 {
        font-size: 25px;
        height: 100%;
        margin: 0;
    }
    p {
        background-color: white;
        box-shadow: 0 0 10px rgb(59, 69, 116);
        border-radius: 30px;
        color: #7c7b7c;
        height: 40px;
        padding: 10px 0;
        text-align: center;
        transition: all 300ms linear;
        width: 60px;
    } 
}

.book__buttons {
    align-items: center;
    display: flex;
    button {
        background-color: rgb(59, 69, 116);
        border-radius: 5px;
        border: none;
        color: whitesmoke;
        font-size: 25px;
        font-weight: bold;
        margin: 0 20px 30px 0;
        padding: 15px 35px;
        transition-property: font-size, box-shadow;
        transition-duration: 0.5s;
        &:hover {
            cursor: pointer;
            box-shadow: 10px 10px 8px 0px rgba(0, 0, 0, 0.1);
            font-size: 26px;
            color: whitesmoke;
        }
    }
}

.book__hero {
    display: flex;
    justify-content: flex-end;
    position: absolute;
    height: 100%;
    bottom: 0;
    right: 0;
    z-index: 0;
}
.book__trian {
    width: 0;
    height: 0;
    border-left: 7vh solid transparent;
    bottom: 0px;
    border-bottom: 80vh solid white;
}
.book__product-img {
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 25vw;
    img {
        height: 25vw;
        transition: all 500ms ease-out;
        &:hover {
            cursor: pointer;
            transform: scale(1.05);
        }
    }
}


@media (max-width: 414px) {
    .book {
        section {
            height: auto;
            width: 100vw;
        }
    }

    .book__hero{
        display: none;
    }

    .book__mobile-img {
        display: block;
    }

    .book__details {
        margin-right: 10px;
        width: 95%;
    }

    .book__price {
        margin-top: 10px;
    }

    .book__buttons {
        flex-direction: column-reverse;
        button {
            margin-left: 15px;
            width: 100%;
        }
    }
}
