.categories__categories {
    display: flex;
    width: 100vw;
    justify-content: center;
    margin-top: 100px;
    section {
        background: rgb(59, 69, 116);
        background: linear-gradient(
            90deg,
            rgba(59, 69, 116, 1) 0%,
            rgba(113, 114, 166, 1) 100%
        );
        border-radius: 15px;
        box-shadow: 0px 0px 30px #c0abe6;
        padding-bottom: 70px;
        width: 75%;
        h2 {
            color: whitesmoke;
            margin-left: 10px;
        }
    }
}

.categories__mapped {
    background-color: white;
    display: grid;
    grid-template-columns: repeat(7, 14.29%);
    width: 100%;
    a {
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 25px;
        text-align: center;
        text-decoration: none;
        transition: all 300ms linear;
        h2 {
            margin-top: 0;
        }
        *, h2 {
            color: black;
            font-size: 30px;
            transition: all 300ms linear;
        }
        &:hover {
            background-color: #c5c5c5;
        }
        &:hover h2, &:hover * {
            color: white;
        }
    }
    
}

@media (max-width: 414px) {
    .categories__categories section {
        width: 100%;
    }
    .categories__mapped {
        grid-template-columns: repeat(3, 33.33%);
        a {
            *, h2 {
                font-size: 20px;
            }
        }
    }
}