

.home {
  display: flex;
  justify-content: center;
  section {
    background-color: white;
    color: black;
    &:not(:last-child) {
      border-bottom: solid 1px #676294;
    }
  }
}

.home__productos {
  background: rgb(59, 69, 116);
  background: linear-gradient(
    90deg,
    rgba(59, 69, 116, 1) 0%,
    rgba(113, 114, 166, 1) 100%
  );
  border-radius: 15px;
  box-shadow: 0px 0px 30px #c0abe6;
  color: white;
  display: flex;
  flex-direction: column;
  margin: 100px 0 30px 0;
  padding-bottom: 70px;
  width: 70%;
  h2 {
    margin-left: 10px;
    font-size: 30px;
  }
}

.home__productos-texto-container {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin: 0 10px;
  .home__ver-mas {
    border: solid 2px #ff7aa5;
    color: #ff7aa5;
    font-size: 20px;
    padding: 5px;
    text-decoration: none;
    text-transform: uppercase;
    transition: all 300ms linear;
    &:hover {
      background: #676294;
      box-shadow: 0 0 10px #676294;
    }
  }
}


.home__libros {
  display: flex;
  flex-wrap: nowrap;
  margin: 0 10px;
  min-height: 450px;
  overflow: auto;
}

.home__book {
  cursor: pointer;
  min-width: 200px;
  width: 200px;
  position: relative;
  a {
    text-decoration: none;
    color: black;
  }
  &:not(:last-child) {
    border-right: solid 1px #676294;
    padding-right: 30px;
  }
  &:not(:first-child) {
    padding-left: 30px;
  }
  img {
    width: 100%;
    height: 280px;
  }
  .home__heart {
    color: #676294;
    opacity: 0;
    position: absolute;
    right: 18px;
    top: 8px;
    transition: all 300ms linear;
    &:hover {
      color: #ff7aa5;
    }
  }
  &:not(:last-child) .home__heart {
    right: 38px;
  }
  p {
    color: #7c7b7c;
    font-size: 12px;
    transition: color 300ms ease-out;
  }
  &:hover p {
    color: black;
  }
  &:hover .home__heart {
    opacity: 1;
  }
}


@media (max-width: 414px) {
  .home__productos {
    border: none;
    width: 100vw;
  }

  .home__productos-texto {
    font-size: 20px;
  }
}
