.profile {
    display: flex;
    justify-content: center;
    margin-top: 100px;
    font-size: 20px;
}

.profile__container {
    background: rgb(59, 69, 116);
    background: linear-gradient(
        90deg,
        rgba(59, 69, 116, 1) 0%,
        rgba(113, 114, 166, 1) 100%
    );
    border-radius: 15px;
    box-shadow: 0px 0px 30px #c0abe6;
    padding-bottom: 70px;
    width: 60%;
    h2 {
        color: whitesmoke;
        margin-left: 10px;
    }
}

.profile__info {
    align-items: center;
    background-color: white;
    display: flex;
    justify-content: flex-start;
    padding: 30px 0 30px 10px;
    margin: 0;
    h2 {
        color: black;
        margin-left: 20px;
    }
    img {
        border-radius: 50%;
        width: 130px;
        height: 130px;
    }
}

.profile__products {
    background-color: white;
    margin: 0;
    h2 {
        color: black;
        margin-top: 0;
    }
    .profile__products-books {
        display: flex;
        padding: 0 10px;
        flex-wrap: nowrap;
    }
}